import Vue from 'vue';
import Router from 'vue-router'

// 组件
import Home from './pages/Home'
import Project from './pages/project';
import Aboutme from './pages/aboutme';
import Demolist from './pages/demolist';
import Cv from './pages/cv';


// 路由
const routes = [{
		path: '/',
		redirect: {
			name: 'aboutme'
		}
	},
	{
		path: '/home',
		component: Home,
		children: [{
				path: '/aboutme',
				component: Aboutme,
				name: 'aboutme',
				meta:{
					title:'关于我'
				}
			},
			{
				path: '/cv',
				component: Cv,
				name: 'cv',
				meta:{
					title:'我的简历'
				}
			},
			{
				path: '/project',
				component: Project,
				name: 'project',
				meta:{
					title:'项目经历'
				}
			},
			{
				path: '/demolist',
				component: Demolist,
				name: 'demolist',
				meta:{
					title:'DEMO'
				}
			}
		]
	},
]

// 创建router实例
Vue.use(Router)

const router = new Router({
	routes
})


// 修改页面title
router.beforeEach((to, from, next) => {
	if(to.meta.title){
		document.title = to.meta.title
	}
	next()
})


export default router;