<template>
    <div class="demolist-page" v-if="pc">
        <div class="view-box">
            <iframe :src="demoUrl[index]" frameborder="0" class="view"></iframe>
        </div>
        <div class="menu">
            <div class="left btn" @click="trsL" v-show="page > 0">
                <i class="iconfont icon-arrow-lift"></i>
            </div>
            <ul class="list" :style="{transform:translate}">
                <li ref="li" @click="index = 0">react井子棋</li>
                <li @click="index = 1">原生JS贪吃蛇</li>
                <li @click="index = 2">vue+Ts简易便签</li>
                <li @click="index = 4">占个位置</li>
                <li @click="index = 5">占个位置</li>
                <li @click="index = 6">占个位置</li>
            </ul>
            <div
                class="right btn"
                @click="trsR"
                v-show="demoUrl.length > 4 &&  page*2 < demoUrl.length-4"
            >
                <i class="iconfont icon-arrow-right"></i>
            </div>
        </div>
    </div>
    <div v-else>该页面请在PC端查看!!!</div>
</template>

<script>
export default {
    data() {
        return {
            demoUrl: [
                "https://wzqup.github.io/ttt/index.html",
                "https://wzqup.github.io/tcs/tcs.html",
                "https://wzqup.github.io/memo/index.html",
                "",
                "",
                ""
            ],
            index: 0,
            page: 0,
            pc: true
        };
    },
    mounted() {
        //isPC?
        if (/(iPhone|iPad|iPod|iOS)/i.test(navigator.userAgent)) {
			this.pc = false;
			return;
        } else if (/(Android)/i.test(navigator.userAgent)) {
			this.pc = false;
			return;
        } else {
			this.pc = true;
		}
		
        // 获取每个菜单的长度
        this.itemW = this.$refs.li.offsetWidth;
        window.onresize = () => {
            this.itemW = this.$refs.li.offsetWidth;
        };
    },
    methods: {
        trsL() {
            this.page--;
        },
        trsR() {
            this.page++;
        }
    },
    computed: {
        translate() {
            return `translateX(-${this.page * 2 * this.itemW}px)`;
        }
    }
};
</script>

<style lang="scss" scoped>
@keyframes bgmove {
    0% {
        background-position-y: 0px, 0%;
    }
    100% {
        background-position-y: -1085px, 0%;
    }
}
.demolist-page {
    position: relative;
    width: 100%;
    height: 100%;
    .view-box {
        position: relative;
        top: 50px;
        margin: 0 auto;
        @include content(80%, 600px, $bgcColor: rgba(255, 0, 0, 0.3));
        border: 10px aqua solid;
        border-radius: 10px;
        .view {
            width: 100%;
            height: 100%;
            background-color: #000;
        }
    }
    .menu {
        position: relative;
        top: 80px;
        @include content(100%, 200px, $bgcColor: rgba(255, 0, 0, 0.3));
        border-top: 10px aqua solid;
        border-bottom: 10px aqua solid;
        .btn {
            position: absolute;
            top: 0;
            bottom: 0;
            z-index: 999;
            background-color: rgba(0, 0, 0, 0.3);
            width: 100px;
            @include flex-center;
            i {
                font-size: 66px;
            }
        }
        .right {
            right: 0px;
        }
        .left {
            left: 0px;
        }
        .list {
            width: 80%;
            height: 100%;
            margin: 0 auto;
            white-space: nowrap;
            background-color: #000;
            transition: all 1s;
            position: relative;
            li {
                width: 25%;
                height: 100%;
                border: 1px #ccc solid;
                text-align: center;
                display: inline-block;
            }
        }
    }
}
.demolist-page::before {
    content: "";
    @include abs-stretch;
    background: url("../../assets/img/bg.png") $mainColor;
    animation: bgmove 40s infinite linear;
}
</style>